"use client"

import { useState } from "react"
import { useSWRConfig } from "swr"
import { useDevices } from "@/lib/hooks/useSWR"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Loader, PlusCircle, AlertCircle } from "lucide-react"
import { DeviceForm } from "./form"

export default function DevicesPage() {
  const { devices, isLoading, error } = useDevices()
  const { mutate } = useSWRConfig()
  const [isFormOpen, setIsFormOpen] = useState(false)
  const [selectedDevice, setSelectedDevice] = useState(null)

  const handleEdit = (device: any) => {
    setSelectedDevice(device)
    setIsFormOpen(true)
  }

  const handleDelete = async (deviceId: string) => {
    if (!confirm("คุณแน่ใจหรือไม่ว่าต้องการลบอุปกรณ์นี้?")) return

    try {
      const res = await fetch(`/api/devices/${deviceId}`, { method: "DELETE" })
      if (!res.ok) throw new Error("ไม่สามารถลบอุปกรณ์ได้")
      mutate("/api/devices") // Revalidate the devices list
    } catch (e: any) {
      alert(e.message)
    }
  }

  const handleFormClose = () => {
    setIsFormOpen(false)
    setSelectedDevice(null)
  }

  return (
    <div className="p-8">
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold text-foreground mb-2">จัดการอุปกรณ์</h1>
          <p className="text-foreground/60">เพิ่ม ลบ หรือแก้ไขอุปกรณ์ IoT ของคุณ</p>
        </div>
        <Button onClick={() => setIsFormOpen(true)}>
          <PlusCircle className="mr-2 h-4 w-4" /> เพิ่มอุปกรณ์ใหม่
        </Button>
      </div>

      <DeviceForm open={isFormOpen} onOpenChange={setIsFormOpen} device={selectedDevice} onClose={handleFormClose} />

      <Card className="p-6 bg-card border-border">
        {isLoading && (
          <div className="flex items-center justify-center py-8">
            <Loader className="w-6 h-6 text-accent animate-spin" />
            <span className="ml-2 text-foreground/60">กำลังโหลดอุปกรณ์...</span>
          </div>
        )}

        {error && (
          <div className="p-4 bg-destructive/10 border border-destructive/20 rounded-lg text-destructive flex items-center">
            <AlertCircle className="mr-2 h-4 w-4" /> เกิดข้อผิดพลาดในการโหลดอุปกรณ์
          </div>
        )}

        {devices && devices.length > 0 ? (
          <div className="space-y-4">
            {devices.map((device: any) => (
              <div
                key={device.id}
                className="flex items-center justify-between p-4 bg-background rounded-lg border border-border hover:border-accent/50 transition"
              >
                <div className="flex items-center gap-4 flex-1">
                  <div className={`w-3 h-3 rounded-full ${device.is_active ? "bg-green-500" : "bg-red-500"}`} />
                  <div className="flex-1">
                    <p className="font-medium text-foreground">{device.name}</p>
                    <p className="text-sm text-foreground/60">
                      {device.location} · {device.type}
                    </p>
                  </div>
                </div>
                <div className="flex items-center gap-4">
                  <p className="text-sm text-foreground/60">{device.mqtt_topic}</p>
                  <div className="flex gap-2">
                    <Button variant="outline" size="sm" onClick={() => handleEdit(device)}>
                      แก้ไข
                    </Button>
                    <Button variant="destructive" size="sm" onClick={() => handleDelete(device.id)}>
                      ลบ
                    </Button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        ) : (
          !isLoading && <div className="text-center py-8 text-foreground/60">ไม่พบอุปกรณ์</div>
        )}
      </Card>
    </div>
  )
}